.modal-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  pointer-events: auto;
}

.modal {
  position: relative;
  padding: 25px 10px;
  background-color: #fff;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  max-width: 98%;
  max-height: 98%;
  overflow: auto;

  @media screen and (max-width: 670px) {
    max-width: 90%;
    max-height: 90%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding: 0 60px;

    @media screen and (max-width: 550px) {
      padding: 0 40px;
    }

    @media screen and (max-width: 440px) {
      padding: 0 30px;
    }

    @media screen and (max-width: 310px) {
      padding: 0 20px;
    }
  }

  .btn--close {
    margin-bottom: 0;
    align-self: end;
  }

  &__img {
    width: 100%;
    height: auto;
    max-width: 400px;
    max-height: 400px;
    margin-bottom: 23px;
    overflow: hidden;
    border-radius: 5px;
    align-self: center;

    @media screen and (max-width: 670px) {
      max-width: 350px;
      max-height: 350px;
    }

    @media screen and (max-width: 550px) {
      max-width: 300px;
      max-height: 300px;
    }

    @media screen and (max-width: 400px) {
      max-width: 200px;
      max-height: 200px;
    }

    @media screen and (max-width: 310px) {
      max-width: 180px;
      max-height: 180px;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    font-size: 20px;
    margin-bottom: 4px;
    width: 400px;
    align-self: center;

    @media screen and (max-width: 670px) {
      max-width: 350px;
    }

    @media screen and (max-width: 550px) {
      max-width: 300px;
    }

    @media screen and (max-width: 400px) {
      max-width: 200px;
      font-size: 16px;
    }

    @media screen and (max-width: 310px) {
      max-width: 180px;
    }
  }

  &__price {
    width: 400px;
    align-self: center;
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 4px;

    @media screen and (max-width: 670px) {
      max-width: 350px;
      font-size: 20px;
    }

    @media screen and (max-width: 550px) {
      max-width: 300px;
    }

    @media screen and (max-width: 400px) {
      max-width: 200px;
      font-size: 16px;
    }

    @media screen and (max-width: 310px) {
      max-width: 180px;
      font-size: 14px;
    }
  }

  &__footer {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;

    @media screen and (max-width: 360px) {
      justify-content: center;
    }

    @media screen and (max-width: 310px) {
      margin-top: 0;
    }
  }

  .btn--add-to-cart {
    padding: 10px 30px;
    font-size: 18px;

    @media screen and (max-width: 610px) {
      padding: 12px 36px;
      font-size: 20px;
    }

    @media screen and (max-width: 450px) {
      font-size: 16px;
    }

    @media screen and (max-width: 390px) {
      padding: 10px 20px;
      font-size: 16px;
    }

    @media screen and (max-width: 310px) {
      width: 100%;
    }
  }
}

.row {
  display: flex;
  justify-content: center;
}

.modal__size {
  align-items: center;
  justify-content: center;
  display: flex;
}
